<template>
  <div id="news">
    <keep-alive>
      <router-view @select="routerLink" v-if="isRouterAlive"></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "news",
  provide(){
      return{
        reload:this.reload
      }
    },
  data() {
    return {isRouterAlive:true,};
  },
  methods: {
    routerLink(path) {
      this.$router.push(path); //路由跳转
    },
    reload(){ //强制跳转
        this.isRouterAlive = false;
        this.$nextTick(function () {
          this.isRouterAlive = true
        });
    },
  },
  components: {},
  
};
</script>

<style lang="scss" scoped>
</style>




