<template>
  <div id="techHome">
    <div id="left">
      <div id="location">
        当前位置：
        <router-link to="/home" class="selectable">服务平台</router-link> >
        <span class="blue">技术</span>
      </div> 
      <div id="baseKnow">
        <div class="bigTitle">基础知识</div>
        <div class="whiteBox" v-if="!message">
            <span class="center">data is loading ... please wait</span>
        </div>
        <div class="whiteBox" v-if="message">
          <li
            @click.stop="routerLinkName('techBasic',i.id,'id')"
            v-if="message"
            v-for="i in message.basic.slice(basicPage*7,basicPage*7+6)"
            class="selectable"
          >{{i.title}}</li>
        </div>
        <div class="selectBox" v-if="message">
          <button
            @click.stop="changeBasicPage(i-1)"
            class="selectButton"
            v-bind:class="{chosen:basicPage+1==i}"
            v-for="i in Math.ceil(message.basic.length/7)"
          >{{i}}</button>
        </div>
      </div>
      <div id="science">
        <div class="bigTitle">技术研讨</div>
        <div class="whiteBox" v-if="!message">
            <span class="center">data is loading ... please wait</span>
        </div>
        <div class="whiteBox" v-if="message">
          <li
            v-if="message"
            v-for="i in message.science.slice(sciencePage*7,sciencePage*7+6)"
            class="selectable"
          >{{i.title}}</li>
        </div>
        <div class="selectBox" v-if="message">
          <button
            @click.stop="changeSciencePage(i-1)"
            class="selectButton"
            v-bind:class="{chosen:sciencePage+1==i}"
            v-for="i in Math.ceil(message.science.length/7)"
          >{{i}}</button>
        </div>
      </div>
    </div>
    <enterpriseRight></enterpriseRight>
  </div>
</template>

<script>
import Vue from "Vue";
import MHeader from "@/components/common/m-header";
import enterpriseRight from "@/components/enterprise/enterpriseRight";

export default {
  name: "productHome",
  data() {
    return {
      message: undefined,
      params: undefined,
      basicPage: 0,
      sciencePage: 0
    };
  },
  components: {
    enterpriseRight
  },
  methods: {
    routerLinkName: function(
      theName,
      paramValue,
      paramName,
      param2Value,
      param2Name
    ) {
      this.params = this.params || new Object();
      this.params[paramName] = paramValue;
      if (param2Name) {
        this.params[param2Name] = param2Value;
      }
      this.params["message"] = this.message;
      this.$router.push({ name: theName, params: this.params });
    },
    changeSciencePage: function(n) {
      this.sciencePage = n;
    },
    changeBasicPage: function(n) {
      this.basicPage = n;
    }
  },
  created: function() {
    if (Vue.prototype.wmessage) {
      this.message = Vue.prototype.wmessage;
    }
    this.axios.get(this.HOST + "common/getIndex").then(res => {
      Vue.prototype.wmessage = res.data.message;
      this.message = res.data.message;
    });
  }
};
</script>

<style lang="scss" scoped>
@mixin border {
  margin: 10px 0;
  padding: 5px;
  max-width: 100%;
  background: rgba(255, 255, 255, 0.8);
}
.whiteBox {
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height:178px;
}
span.center{
    text-align:center;
}
div#techHome {
  width: 1200px;
  margin: 0 auto;
}
div#baseKnow,
div#science {
  @include border;
}
div.bigTitle {
  margin-bottom: 5px;
}
li {
  width: 690px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-top:6px;
  margin-left:5px;
  margin-right:5px;
  padding-bottom:2px;
  border-bottom:1px solid rgb(222,222,222);
}
div.selectBox {
  background: white;
  padding: 5px;
}
button.selectButton {
  border: 1px solid black;
  outline: none;
  margin-right: 5px;
  cursor: pointer;
}
button.chosen {
  background: black;
  color: white;
}
</style>



