<template>
  <div id="techBasic">
    <div id="left">
      <div id="location">
        当前位置：
        <router-link to="/home" class="selectable">服务平台</router-link> >
        <router-link to="/tech" class="selectable">技术</router-link> >
        <span class="blue">基础知识</span>
      </div>
      <div v-if="message" class="outBox">
        <h1>{{message.information.title}}</h1>
        <div class="whiteBox textBox" v-html="message.information.content[pageNum].content">
        </div>
      </div>
    </div>
    <enterpriseRight></enterpriseRight>
  </div>
</template>
<script>
import Vue from 'vue';
import enterpriseRight from "@/components/enterprise/enterpriseRight";

export default {
  name: "basic",
  data() {
    return {
      message:undefined,
      pageNum:0
    };
  },
  components: {
    enterpriseRight
  },
  created: function() {
    this.axios.get(this.HOST + '/information/getBasicInfo',{
      params:{
        id:this.$route.params.id
      }
    })
    .then((res)=>{
      this.message=res.data.message;
    })
  }
};
</script>
<style lang="scss" scoped>
*{
  margin:0;
  padding:0;
}
div#techBasic /deep/ img{
  max-width:650px;
}
div#techBasic{
  width:1200px;
  margin:0 auto;
  padding-bottom:10px;
  clear:both;
}
h1{
  background:white;
  text-align:center;
  padding:20px 0;
}
div.outBox{
  width:700px;
  padding:5px;
  padding-top:0;
  background:rgba(255,255,255,0.8);
  margin-bottom:10px;
}
div.textBox{
  background:white;
  padding:5px;
}
</style>


