<template>
  <div id="tech">
    <router-view></router-view>
  </div>
</template>

<script>
import MHeader from "@/components/common/m-header";

export default {
  name: "tech",
  data() {
    return {};
  },
  components: {
    MHeader
  }
};
</script>

<style lang="scss">
div#tech{
  clear:both;
}
div#left {
  display: inline-block;
  float: left;
}
.blue {
  color: #127de7;
}
.more {
  float: right;
  color: #127de7;
  cursor: pointer;
}
.selectable {
  cursor: pointer;
}
.selectable:hover {
  color: #127de7;
}
.smallLink:hover {
  cursor: pointer;
  color: #127de7;
}
.cursordefault {
  cursor: pointer;
}
div#location {
  background: rgba(255, 255, 255, 0.8);
  margin-top: 10px;
  padding: 5px;
  width: 700px;
}
</style>




