<template>
  <div id="association">
    <keep-alive>
      <router-view @select="routerLink" v-if="isRouterAlive"></router-view>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: "association",
  provide(){
      return{
        reload:this.reload
      }
    },
  data() {
    return {isRouterAlive:true,//强制跳转
    };
  },
  methods: {
    routerLink(path) {
      this.$router.push(path); //路由跳转
    },
    reload(){
        this.isRouterAlive = false;
        this.$nextTick(function () {
          this.isRouterAlive = true
        });
    },  
  },
  components: {}
};
</script>

<style lang="scss" scoped>
</style>




